(function () {
    'use strict';
    var angular = require('angular');
    require('./max-otp.component.scss');

    var maxOtp = angular.module('app').component('maxOtp', {
        template: require('./max-otp.component.html'),
        controller: ['$anchorScroll', 'appState', function ($anchorScroll, appState) {
            var vm = this;

            vm.$onInit = function () {
                appState.clearAll();
                $anchorScroll();
            };
            vm.close = function () {
                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = maxOtp.name;
})();


